import './App.css';
import HeaderComponent from './components/header/header';
import InterestsComponent from './components/interests/interests';
import ProjectsComponent from './components/projects/projects';
import ExperienceComponent from './components/experience/experience';
import { Container } from 'react-bootstrap';
import {
  BrowserRouter as Router,
  // HashRouter as Router,
  Switch,
  Route
} from "react-router-dom";

const Home = () => <div>home</div>
const NoMatch = () => <div>404</div>

function App() {

  return (
    <div className="App">
      <HeaderComponent></HeaderComponent>
      <Container>
        <Router basename={process.env.PUBLIC_URL}>
          <Switch>
            <Route path="/" exact><Home/></Route>
            <Route path="/interests"><InterestsComponent></InterestsComponent></Route>
            <Route path="/experience"><ExperienceComponent/></Route>
            <Route path="/projects/side"><ProjectsComponent/></Route>
            <Route path="/projects/school"><ProjectsComponent/></Route>
            <Route path="/projects/contracts"><ProjectsComponent/></Route>
            <Route path="*"><NoMatch/></Route>
          </Switch>
  	    </Router>
      </Container>
    </div>
  );
}

export default App;
